@font-face {
  font-weight: 500;
  font-family: "Arial";
  font-style: normal;
  font-display: swap;
  src: url("../fonts/arial.woff2") format("woff2"), url("../fonts/arial.woff") format("woff");
}
@font-face {
  font-family: "ogg";
  font-style: normal;
  font-display: swap;
  src: url("../fonts/ogg.woff") format("woff");
}
@font-face {
  font-family: "oggr";
  font-style: normal;
  font-display: swap;
  src: url("../fonts/Ogg-Roman.woff") format("woff");
}
@font-face {
  font-family: "Untm";
  font-style: normal;
  font-display: swap;
  src: url("../fonts/UntitledSans-Medium.woff") format("woff");
}
@font-face {
  font-family: "Untr";
  font-style: normal;
  font-display: swap;
  src: url("../fonts/UntitledSans-Regular.woff") format("woff");
}
@font-face {
  font-family: "Untr";
  font-style: normal;
  font-display: swap;
  src: url("../fonts/UntitledSans-Regular.woff") format("woff");
}
@font-face {
  font-family: "Untl";
  font-style: normal;
  font-display: swap;
  src: url("../fonts/UntitledSans-Light.woff") format("woff");
}
*,
*::before,
*::after {
  box-sizing: inherit;
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  vertical-align: baseline;
  background: transparent;
  outline: none;
}
html {
  box-sizing: border-box;
}
a {
  text-decoration: none;
}
ul {
  list-style: none;
}
@mixin list {
  list-style: none;
  margin: 0;
  padding: 0;
}
@mixin reset {
  margin: 0;
  padding: 0;
}
.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}
.header {
  position: fixed;
  padding: 30px 30px 30px;
  width: 100%;
  z-index: 3;
}
.header__nav {
  position: relative;
  display: grid;
  grid-template-columns: min-content 1fr;
  grid-template-rows: 30px;
  align-items: center;
}
.header__logo {
  font-family: "Untl";
  font-size: 18px;
  line-height: 18px;
  z-index: 2;
  letter-spacing: -0.02em;
  color: inherit;
}
.header__button {
  position: relative;
  display: flex;
  justify-self: end;
  z-index: 2;
  letter-spacing: -0.02em;
}
.header__button-span {
  font-family: "Untl";
  font-size: 18px;
  line-height: 18px;
  transition-duration: 0.6s;
}
.header__button-span--nav {
  transform: translate3d(50%, 0, 0);
  opacity: 1;
}
.header__button-span--close {
  transform: translate3d(-200%, 0, 0);
  opacity: 0;
}
.header__button-span--nav-mod {
  transform: translate3d(0, 0, 0);
  opacity: 1;
}
.header__button-span--close-mod {
  transform: translate3d(-50%, 0, 0);
  opacity: 0;
}
.header__list-main {
  display: grid;
  grid-template-rows: repeat(3, 1fr);
  grid-row-gap: 5%;
  background-color: white;
  position: fixed;
  justify-items: end;
  left: 0;
  top: 0;
  height: 100vh;
  width: 100%;
  padding: 20% 30px;
  z-index: 1;
  opacity: 0;
  will-change: transform;
  animation-name: lift-down;
  animation-duration: 0.6s;
  transition-duration: 0.6s;
  animation-fill-mode: forwards;
  animation-timing-function: cubic-bezier(0.7, 0.8, 0, 1);
}
.header__list-main--open {
  opacity: 1;
  animation-name: lift-up;
}
.header__list-main--open .header__item-main:nth-child(1) {
  animation-name: lift-up;
}
.header__list-main--open .header__item-main:nth-child(2) {
  animation-name: lift-up;
  animation-delay: 76ms;
}
.header__list-main--open .header__item-main:nth-child(3) {
  animation-name: lift-up;
  animation-delay: 157ms;
}
.header__item-main {
  display: flex;
  animation-name: lift-down;
  animation-duration: 0.8s;
  animation-fill-mode: forwards;
}
.header__item-main:first-child {
  align-self: flex-end;
}
.header__item-main:last-child {
  align-self: flex-start;
}
@keyframes lift-up {
  from {
    transform: translate3d(120%, 0, 0);
  }
  to {
    transform: translate3d(0);
  }
}
@keyframes lift-down {
  from {
    transform: translate3d(0);
  }
  to {
    transform: translate3d(120%, 0, 0);
  }
}
.header__button-theme {
  font-family: "Untl";
  grid-column-start: 2;
  text-align: end;
  align-items: flex-start;
  font-size: 18px;
  line-height: 18px;
  letter-spacing: -0.02em;
  z-index: 2;
}
.header__list {
  display: flex;
  flex-flow: column;
  align-items: flex-end;
}
.header__list--one-list .header__item {
  padding: 10px 0;
}
.header__list--one-list .header__link {
  color: inherit;
}
.header__list--two-list {
  flex-flow: row wrap;
  justify-content: flex-end;
}
.header__list--two-list .header__item {
  position: relative;
  padding: 0 0 15px 15px;
}
.header__list--two-list .header__link {
  font-family: "Untl";
  font-size: 18px;
  line-height: 18px;
  color: inherit;
  letter-spacing: -0.02em;
}
.header__link {
  font-size: 3em;
  line-height: 45px;
}
.header__icon {
  margin-left: -3px;
  vertical-align: top;
  fill: rgb(100 100 100);
}
.header__link-button {
  font-size: 18px;
  line-height: 18px;
  color: inherit;
}
.skills {
  padding: 30px;
}
.intro {
  position: relative;
  overflow: hidden;
  padding: 30px 0;
}
.intro__title {
  font-family: "oggr", serif;
  font-size: 6rem;
  font-size: 8em;
  line-height: 1;
  font-weight: 300;
  text-align: center;
  margin-bottom: 30px;
  letter-spacing: -5px;
  user-select: none;
}
.intro__desc {
  font-family: "Untl", sans-serif;
  font-size: 16px;
  line-height: 150%;
  color: rgb(134 134 139);
  padding: 30px;
}
.intro__photo {
  width: 100%;
  height: auto;
  padding: 0 30px;
}
.skills__title {
  font-size: 30px;
  line-height: 30px;
  font-weight: 700;
  margin-bottom: 15px;
}
.skills__list {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
}
.skills__item {
  padding: 5px 0;
}
.skills__desc {
  font-family: "Lusitana", "Arial", sans-serif;
  font-size: 16px;
  line-height: 150%;
  color: rgb(134 134 139);
}
.background {
  position: fixed;
  top: 0px;
  left: 0px;
  width: 100%;
  height: 100%;
  overflow: hidden;
  z-index: -1;
  backface-visibility: hidden;
}
.background__title {
  position: absolute;
  font-size: 80rem;
  top: 44rem;
  left: -9rem;
  opacity: 0.4;
  color: rgb(62 62 62);
  font-weight: 300;
}
.index-page {
  position: relative;
  min-width: 320px;
  font-family: "oggr";
  font-size: 14px;
  line-height: 14px;
  font-weight: 400;
  font-style: normal;
  color: rgb(64 64 64);
  background-color: white;
}
button {
  font-family: inherit;
  color: inherit;
}
.is-locked {
  overflow: hidden;
}
.dark {
  color: #ebebeb;
  background-color: #0f0f0f;
}
.dark__button {
  color: #ebebeb;
}
.dark__border {
  border-color: #ebebeb;
}
.index-main {
  padding-top: 90px;
}
.dark__border {
  border-color: white;
}
